// import { Typography } from '@material-ui/core';
import axios from 'axios';
import { useState } from 'react';
import { Container, Form, Nav, Navbar, Button, Row, Col, Table } from 'react-bootstrap';

const HomeScreen = () => {
    const [apiData, setApiData] = useState([]);
    const [files, setFiles] = useState([]);
    const [error, setError] = useState('');
    const [viewChange, setViewChange] = useState(false);

    const formData = new FormData();
    formData.append('file', files[0]);

    const handleSubmit = async () => {
        try {
            if (files.length) {
                const { data } = await axios.post('/api/v1/home', formData);
                setApiData(data.data.rows);
                setError('');
                setFiles([]);
            } else {
                setError('You have not choose any excel file');
            }
        } catch (err) {
            setError(err.response.data.message);
        }
    };

    return (
        <>
            <Navbar bg="light" expand="lg">
                <Container>
                    <Navbar.Brand href="#home">Excel File Reader</Navbar.Brand>
                    <Navbar.Toggle aria-controls="basic-navbar-nav" />
                    <Navbar.Collapse id="basic-navbar-nav">
                        <Nav className="ml-auto">
                            <Nav.Link href="#home">Home</Nav.Link>
                            <Nav.Link href="#link">Link</Nav.Link>
                        </Nav>
                    </Navbar.Collapse>
                </Container>
            </Navbar>

            <Container>
                <section className="mt-5">
                    <Row>
                        <Col xs={12} md={6}>
                            <div className="w-100  mb-3 float-left">
                                <Form.File id="formcheck-api-custom" custom>
                                    <Form.File.Input
                                        isInvalid={!!error}
                                        onChange={(e) => {
                                            setFiles(e.target.files);
                                            setError('');
                                        }}
                                    />
                                    <Form.File.Label data-browse="Select a file">
                                        {files[0]?.name ?? 'Select an Excel file'}
                                    </Form.File.Label>
                                    <Form.Control.Feedback type="Invalid" style={{ color: 'red' }}>
                                        {error || ''}
                                    </Form.Control.Feedback>
                                </Form.File>
                            </div>
                        </Col>
                        <Col xs={12} md={2}>
                            <Button variant="primary" className="w-100 mb-3" onClick={handleSubmit}>
                                Upload
                            </Button>
                        </Col>
                        <Col xs={12} md={2}>
                            <Button
                                variant="primary"
                                className="w-100 mb-3"
                                onClick={() => setViewChange(!viewChange)}
                            >
                                Change View
                            </Button>
                        </Col>
                        <Col xs={12} md={2}>
                            <Button variant="primary" className="w-100 mb-3">
                                <a
                                    href={`${window.location.href}/uploads/document.docx`}
                                    download
                                    className="text-white"
                                    style={{ textDecoration: 'none' }}
                                >
                                    Download as Doc
                                </a>
                            </Button>
                        </Col>
                    </Row>
                </section>
            </Container>
            <Container className="text-center">
                <h4>{files[0]?.name ? `${files[0]?.name}` : ''}</h4>
                <section style={{ display: viewChange ? 'block' : 'none' }}>
                    <Table striped bordered hover responsive>
                        <tbody>
                            {apiData.map((data, dataIndex) => (
                                <tr key={`${data + dataIndex + Math.random() * 10}`}>
                                    {data.map((singleData, index) => (
                                        <td key={`${singleData + index + Math.random() * 10}`}>
                                            {singleData}
                                        </td>
                                    ))}
                                </tr>
                            ))}
                        </tbody>
                    </Table>
                </section>
            </Container>

            <Container style={{ display: viewChange ? 'none' : 'block' }}>
                {apiData.map((data, dataIndex) => (
                    <>
                        {data.map((singleData, index) => (
                            <>
                                {index === 0 ? (
                                    <>
                                        <br />
                                        <h4 style={{ color: 'blue' }}>
                                            {dataIndex !== 0
                                                ? `Data Row ${dataIndex}`
                                                : 'Data Title'}
                                        </h4>
                                        <h5>
                                            {index + 1}.{singleData}
                                        </h5>{' '}
                                    </>
                                ) : (
                                    <h5>
                                        {index + 1}.{singleData}
                                    </h5>
                                )}
                            </>
                        ))}
                    </>
                ))}
            </Container>
        </>
    );
};

export default HomeScreen;
